<template>
  <div class="my_special_container">
    <div class="my_special_top">
      <van-nav-bar title="我的学习" @click-left="onClickLeft">
        <template #left >
          <span style="font-size:18px;">&lt;</span>
        </template>
        <template #right>
          <van-icon name="calender-o" size="20" color="black" />
        </template>
      </van-nav-bar>
    </div>
    <div class="my_special_num">
      <div
        :class="selectIndex===index?'my_special_num_items_active':'my_special_num_items'"
        v-on:click="onCourse(index)"
        v-for="(item,index) in courseType"
        :key="index"
      >
        <div style="height:45px;line-height:45px;">{{item.name}}({{item.num}})</div>
        <div
          :class="selectIndex===index?'my_special_num_items_footer_active':'my_special_num_items_footer'"
        ></div>
      </div>
    </div>
    <div class="my_special_content">
      <div class="my_special_items" v-for="(item,index) in contentList" :key="index">
        <div style="font-size:18px;">{{item.title}}</div>
        <div class="course_main_items_time">
          <span>
            <img src="../assets/zhong.png" style="width:20px;height:20px;margin:0 5px;" alt />
          </span>
          <span class="course_main_items_time.date">{{item.date}}</span>
          <span
            style="margin-left:5px;"
          >{{item.start_play_date | time1}} ~ {{item.end_play_date | time2}}</span>
          <span style="margin-left:5px;">|</span>
          <span style="margin-left:5px;">共{{item.section_num}}课时</span>
        </div>
        <div class="my_special_items_footer">
          <div class="my_special_items_fooer_line"></div>
          <div class="my_special_items_footer_char">已学习{{item.progress_rate}}%</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      courseType: [],
      contentList: [],
      selectIndex: 0
    };
  },
  methods: {
    onCourse(index) {
      this.selectIndex = index;
      this.showFooter = !this.showFooter;
      if (this.selectIndex == 1) {
        this.$http.get("/api/app/myStudy/5").then(res => {
          this.courseType = res.data.data.typeNum;
          this.contentList = res.data.data.courseList;
        //   window.console.log(this.contentList);
        //  window.console.log(this.courseType);
        });
      }
    },
    onClickLeft(){
        this.$router.back()
    }
  },
  mounted() {
    this.$http.get(`/api/app/myStudy/2`).then(res => {
      //   window.console.log(res.data);
      this.courseType = res.data.data.typeNum;
      this.contentList = res.data.data.courseList;
    //   window.console.log(this.contentList);
    //   window.console.log(this.courseType);
    });
  },
  filters: {
    price(val) {
      if (val == 0) {
        return "免费";
      } else {
        return `${(val / 100).toFixed(2)}`;
      }
    },
    time1(val) {
      let date = new Date(val * 1000);
      let m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      let h = date.getHours();
      h = h < 10 ? "0" + h : h;
      let minute = date.getMinutes();
      minute = minute < 10 ? "0" + minute : minute;
      return m + "月" + d + "日" + " " + h + ":" + minute;
    },
    time2(val) {
      let date = new Date(val * 1000);
      let m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      let h = date.getHours();
      h = h < 10 ? "0" + h : h;
      let minute = date.getMinutes();
      minute = minute < 10 ? "0" + minute : minute;
      return m + "月" + d + "日" + " " + h + ":" + minute;
    }
  }
};
</script>

<style>
.my_special_container {
  width: 100vw;
  height: 100vh;
}
.my_special_top {
  width: 100%;
  height: 50px;
  border-bottom: 1px solid rgb(240, 240, 240);
}
.my_special_num {
  width: 100%;
  height: 50px;
  /* background: saddlebrown; */
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-bottom: 1px solid rgb(240, 240, 240);
}
.my_special_num_items {
  width: 70px;
  margin-left: 10px;
}
.my_special_num_items_active {
  width: 70px;
  margin-left: 10px;
  color: rgb(235, 97, 0);
}
.my_special_num_items_footer {
  width: 50px;
  height: 2px;
  background: none;
  margin-left: 5px;
}
.my_special_num_items_footer_active {
  width: 50px;
  height: 2px;
  background: rgb(235, 97, 0);
  margin-left: 5px;
}
.my_special_content {
  width: 100%;
  /* height: 200px; */
  /* background:rgb(240, 240, 240); */
}
.my_special_items {
  width: 90%;
  height: 120px;
  margin: 10px 5%;
  background: white;
  box-shadow: 0px 0px 20px rgb(230, 230, 230);
  padding: 15px 0;
}
.my_special_items_footer {
  width: 100%;
  height: 80px;
  /* background:rosybrown; */
  display: flex;
  align-items: center;
}
.my_special_items_fooer_line {
  width: 100px;
  height: 3px;
  background: rgb(239, 240, 240);
  margin-left: 10px;
}
.my_special_items_footer_char {
  font-size: 14px;
  color: grey;
  margin-left: 10px;
}
</style>